import React, { useEffect, useState } from "react";
import { containSlang } from "../../utils/validation";
import { handleKeyDown } from "../../utils/keyDownHandler";
import axios from "axios";
import {
  getItemCategories,
  getItemSizes,
  postImageUpload,
  postItemData,
} from "../../api/api";

// style
import {
  BtnBox,
  ThumbNailImg,
  ThumbNailImgBox,
  ThumbNailImgText,
  ImgBox,
  InfoInput,
  InfoLabel,
  ItemImage,
  ItemImagesBox,
  ItemInfo,
  ItemInfoBox,
  RegisterInfo,
  Container,
  InfoInputScript,
  ItemInfoScript,
  ImgUploadBtn,
} from "../../styles/userProfileStyle/itemRegisterStyle";
import UserInput from "./UserInput";
import { UniBtn } from "../button/UniBtn";
import { ErrorMessage } from "../error/ErrorMessage";

const ItemRegister = () => {
  const [formData, setFormData] = useState({
    name: "",
    price: "",
    expired_at: "",
    description: "",
    category: "",
    size_name: "",
    stuck: "",
    images_url: [],
  });

  const [validImages, setValidImages] = useState([]);
  const [thumbNailImg, setThumbNailImg] = useState(null);
  const [imgError, setImgError] = useState("");
  const [slangError, setSlangError] = useState({});
  const [notifyMsg, setNotifyMsg] = useState("");

  const [itemId, setItemId] = useState(null);
  const [categories, setCategories] = useState([]);
  const [sizes, setSizes] = useState([]);

  // 
  const handleImagesChange = (e) => {
    const files = Array.from(e.target.files);
    const allowedTypes = ["image/jpeg", "image/jpg", "image/png"];
    const newValidImages = [];
    setImgError("");

    files.forEach((file) => {
      //  
      if (!allowedTypes.includes(file.type)) {
        setImgError(
          "jpg, jpeg, png      ."
        );
      }
      // (1MB) 
      else if (file.size > 1 * 1024 * 1024) {
        setImgError("   1MB   .");
      } else {
        newValidImages.push(file);
      }
    });

    setValidImages((prev) => [...prev, ...newValidImages]);
    // setValidImages(newValidImages);

    //  = 
    if (newValidImages.length > 0) {
      setThumbNailImg(URL.createObjectURL(newValidImages[0]));
    }
  };

  const handleInputChange = (e) => {
    const { id, value } = e.target;
    setFormData((prev) => ({
      ...prev,
      [id]: value,
    }));

    //  
    if (containSlang(value)) {
      setSlangError((prev) => ({
        ...prev,
        [id]: "   .",
      }));
    } else {
      setSlangError((prev) => ({
        ...prev,
        [id]: "",
      }));
    }

    //  
    if (id === "description" && value.length < 10) {
      setSlangError((prev) => ({
        ...prev,
        description: "  10   .",
      }));
    } else if (id === "description") {
      setSlangError((prev) => ({
        ...prev,
        description: "",
      }));
    }
  };

  //  >>  POST >> 
  const handleImageUpload = async () => {
    //  
    const ImageDataUpload = new FormData();

    if (validImages.length > 0) {
      validImages.forEach((image) => {
        ImageDataUpload.append("images", image);
      });
    }

    try {
      const res = await postImageUpload(ImageDataUpload);
      setItemId(res.itemId);
      console.log(": ", res.itemId);
      setNotifyMsg(" !");
      return res;
    } catch (error) {
      console.error(": ", error.message);
      setNotifyMsg(" .");
      return;
    }
  };

  //  >>  POST
  const handleSubmit = async (e) => {
    e.preventDefault();

    const isSlangValid = Object.values(slangError).every(
      (error) => error === ""
    );

    if (!isSlangValid) {
      const errorInputs = [
        { id: "name", error: slangError.name },
        { id: "description", error: slangError.description },
      ];
      const firstErrorInput = errorInputs.find((input) => input.error);
      if (firstErrorInput) {
        document.getElementById(firstErrorInput.id).focus();
      }

      setNotifyMsg("   .");
      return;
    }

    //  
    const uploadedImageUrls = await handleImageUpload();

    //   
    const jsonData = {
      name: formData.name,
      price: parseFloat(formData.price),
      expired_at: formData.expired_at + "T00:00:00", //  
      description: formData.description,
      category: formData.category,
      size_name: formData.size_name,
      stuck: parseInt(formData.stuck, 10),
      images_url: uploadedImageUrls,
    };

    try {
      const res = await postItemData(jsonData);
      setItemId(res.itemId);
      console.log(": ", res);
      setFormData(res);
      setNotifyMsg(" !");
    } catch (error) {
      console.error(": ", error.message);
      setNotifyMsg(" .");
      return;
    }
  };

  //   GET >> 
  const fetchCategories = async () => {
    try {
      const res = await getItemCategories();
      console.log(res);
      setCategories(res.categoryList);
    } catch (error) {
      console.error("  .", error.message);
    }
  };
  //   GET >> 
  const fetchSizes = async () => {
    try {
      const res = await getItemSizes();
      console.log(res);
      setSizes(res.sizeItemList);
    } catch (error) {
      console.error("  .", error.message);
    }
  };

  useEffect(() => {
    fetchCategories();
    fetchSizes();
  }, []);

  return (
    <Container onSubmit={handleSubmit}>
      <BtnBox>
        {notifyMsg && <ErrorMessage>{notifyMsg}</ErrorMessage>}
        <UniBtn type="submit"></UniBtn>
      </BtnBox>
      <RegisterInfo>
        <ThumbNailImgBox>
          {thumbNailImg ? (
            <ThumbNailImg src={thumbNailImg} alt="" />
          ) : (
            <ImgBox> .</ImgBox>
          )}
          <ThumbNailImgText>.</ThumbNailImgText>
        </ThumbNailImgBox>
        <ItemInfoBox>
          <ItemInfo>
            <InfoLabel></InfoLabel>
            <InfoInput
              type="file"
              id="img"
              multiple
              onChange={handleImagesChange}
              required
            />
            <ImgUploadBtn type="button" onClick={handleImageUpload}>
              
            </ImgUploadBtn>
            {validImages.length > 0 && (
              <ItemImagesBox>
                {/*        */}
                {validImages.map((file, idx) => (
                  <ItemImage
                    key={idx}
                    src={URL.createObjectURL(file)}
                    alt={`-${idx + 1}`}
                    className={idx === 0 ? "thumbnail" : ""}
                  />
                ))}
              </ItemImagesBox>
            )}
          </ItemInfo>
          {imgError && <ErrorMessage>{imgError}</ErrorMessage>}
          <UserInput
            label=""
            id="name"
            type="text"
            placeholder=" "
            value={formData.name}
            onChange={handleInputChange}
            onKeyDown={handleKeyDown}
            error={slangError.name}
          />
          <UserInput
            label=""
            id="price"
            type="number"
            placeholder="990  "
            min="990"
            value={formData.price}
            onChange={handleInputChange}
            onKeyDown={handleKeyDown}
          />
          <UserInput
            label=""
            id="category"
            type="select"
            value={formData.category}
            onChange={handleInputChange}
            onKeyDown={handleKeyDown}
            error={null}
            options={[
              { value: "", label: "----" },
              ...categories.map((category) => ({
                value: category.id,
                label: category.categoryName,
              })),
            ]}
          />
          <UserInput
            label=""
            id="size_name"
            type="select"
            value={formData.size_name}
            onChange={handleInputChange}
            onKeyDown={handleKeyDown}
            error={null}
            options={[
              { value: "", label: "----" },
              ...sizes.map((size) => ({
                value: size.id,
                label: size.sizeName,
              })),
            ]}
          />
          <UserInput
            label=""
            id="stuck"
            type="number"
            placeholder="1  "
            min="1"
            value={formData.stuck}
            onChange={handleInputChange}
            onKeyDown={handleKeyDown}
          />
          <UserInput
            label=""
            id="expired_at"
            type="date"
            min={new Date().toISOString().split("T")[0]}
            value={formData.expired_at}
            onChange={handleInputChange}
            onKeyDown={handleKeyDown}
          />
          <ItemInfoScript>
            <InfoLabel></InfoLabel>
            <InfoInputScript
              id="description"
              as="textarea"
              placeholder=" 10 ."
              value={formData.description}
              onChange={handleInputChange}
              onKeyDown={handleKeyDown}
              required
            />
            {slangError.description && (
              <ErrorMessage>{slangError.description}</ErrorMessage>
            )}
          </ItemInfoScript>
        </ItemInfoBox>
      </RegisterInfo>
    </Container>
  );
};

export default ItemRegister;
