import React, { useEffect, useState } from "react";
import { useNavigate, useParams } from "react-router-dom";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import { getItemImageById, getItemById, patchCartItem } from "../api/api";
import {
  Wrapper,
  Image,
  Name,
  Date,
  ImageContainer,
  InfoContainer,
  Icon,
  IconImage,
  Product,
  ProductInfo,
  ProductName,
  ProductPrice,
  ProductSize,
  ProductCheck,
  ProductCheckButton,
  DeleteButton,
  Count,
  Option,
  QuantityButton,
  OptionBox,
  DeliveryText,
  Button,
  ModalOverlay,
  ModalContent,
  ModalCloseContent,
  ModalCloseWord,
  ButtonContainer,
  ModalButton,
  ModalCloseButton,
  ProductDescription,
} from "../styles/ProductDetailStyle";

import HeartIcon from "../icons/heart.png";
import StoreIcon from "../icons/store.png";

const ProductDetail = () => {
  const { item_id } = useParams();
  const [itemImages, setItemImages] = useState([]);
  const [itemData, setItemData] = useState("");
  const navigate = useNavigate();

  const [selectedSize, setSelectedSize] = useState("");
  const [quantity, setQuantity] = useState(1);
  const [ModalOpen, setModalOpen] = useState(false);
  const [ModalType, setModalType] = useState("");
  const [productInfo, setProductInfo] = useState(null); //    

  //  
  const handleSizeChange = (event) => {
    setSelectedSize(event.target.value);
  };

  //   (1    )
  const decreaseQuantity = () => {
    setQuantity((prevQuantity) => Math.max(1, prevQuantity - 1));
  };

  //   (10    )
  const increaseQuantity = () => {
    setQuantity((prevQuantity) => Math.min(10, prevQuantity + 1));
  };

  //    
  const handleDeleteSelection = () => {
    setSelectedSize("");
    setQuantity(1);
  };

  //      
  const handleAddToCart = (event) => {
    event.preventDefault();

    if (!selectedSize || quantity <= 0) {
      setModalType("warning");
      setModalOpen(true);
    } else {
      const info = {
        itemName: itemData.item_name,
        itemPrice: itemData.item_price,
        itemSize: selectedSize,
        itemQuantity: quantity,
        itemImage: itemImages[0]?.imageUrl,
      };

      setProductInfo(info);
      setModalType("added");
      setModalOpen(true);
    }
  };

  const closeModal = () => {
    setModalOpen(false);
  };
  const handleCartPatch = async () => {
    try {
      const itemStockId = itemData.size_stock_list[0].item_stock_id;
      await patchCartItem(itemStockId, quantity); //  PATCH 
      console.log("  .");
    } catch (error) {
      console.error("    :", error);
    }
  };
  //  
  const handleGoToCart = async () => {
    await handleCartPatch();
    navigate("/Basket", { state: { product: productInfo } });
  };

  //   
  const handleGoToPayment = () => {
    navigate("/Payment");
  };

  const settings = {
    dots: true,
    infinite: true,
    speed: 400,
    slidesToShow: 1,
    slidesToScroll: 1,
    autoplay: true,
    autoplaySpeed: 3000,
  };

  useEffect(() => {
    const fetchImages = async () => {
      try {
        const response = await getItemImageById(item_id);
        const response2 = await getItemById(item_id);
        setItemData(response2);
        setItemImages(response.itemImageResponses); // API   
        //console.log(response);
        console.log(response2);
        setItemImages(response.itemImageResponses);
        setItemData(response2);

        console.log("Fetched itemImages:", response.itemImageResponses);
      } catch (error) {
        console.error("Error fetching images:", error);
      }
    };

    fetchImages();
  }, [item_id]);

  return (
    <>
      <Wrapper>
        <ImageContainer>
          <Image>
            <Slider {...settings}>
              {itemImages.length > 0 ? (
                [...Array(3)].map((_, index) => (
                  <div key={index}>
                    <img src={itemImages[0].imageUrl} alt={`Image ${itemImages[0].imageUrlId}`} />
                  </div>
                ))
              ) : (
                <p>No images available</p>
              )}
            </Slider>
          </Image>
          <Date></Date>
        </ImageContainer>
        <InfoContainer>
          <Icon>
            <Name>{itemData.seller_nickname} </Name>
            <IconImage src={StoreIcon} alt="StoreIcon" />
            <IconImage src={HeartIcon} alt="HeartIcon" />
          </Icon>
          <Product>
            <ProductName>{itemData.item_name}</ProductName>
            <ProductPrice>{itemData.item_price}</ProductPrice>
            <ProductInfo>
              <ProductSize>
                <Option>
                  <select onChange={handleSizeChange}>
                    <option value="S">S</option>
                    <option value="M">M</option>
                    <option value="L">L</option>
                  </select>
                </Option>
                <QuantityButton>
                  <ProductCheckButton onClick={decreaseQuantity}></ProductCheckButton>
                  <Count>{quantity}</Count>
                  <ProductCheckButton onClick={increaseQuantity}></ProductCheckButton>
                </QuantityButton>
              </ProductSize>

              {selectedSize && (
                <ProductCheck>
                  <p>{itemData.item_name}</p>
                  <p>{itemData.item_price}</p>
                  <p>: {selectedSize}</p>
                  <p>: {quantity}</p>
                  <DeleteButton onClick={handleDeleteSelection}></DeleteButton>
                </ProductCheck>
              )}
            </ProductInfo>
            <ProductDescription>  : {itemData.description}</ProductDescription>
            <DeliveryText>  10     </DeliveryText>
            <DeliveryText>       3000</DeliveryText>
          </Product>
          <OptionBox>
            <Button onClick={handleAddToCart}> </Button>
            <Button onClick={handleGoToPayment}> </Button>
          </OptionBox>
        </InfoContainer>
      </Wrapper>

      {ModalOpen && ModalType === "warning" && (
        <ModalOverlay>
          <ModalCloseContent>
            <ModalCloseButton onClick={closeModal}>x</ModalCloseButton>
            <ModalCloseWord>  </ModalCloseWord>
          </ModalCloseContent>
        </ModalOverlay>
      )}

      {ModalOpen && ModalType === "added" && (
        <ModalOverlay>
          <ModalContent>
            <p> </p>
            <ButtonContainer>
              <ModalButton onClick={closeModal}> </ModalButton>
              <ModalButton
                onClick={() => {
                  closeModal();
                  handleGoToCart(); //  
                }}
              >
                 
              </ModalButton>
            </ButtonContainer>
          </ModalContent>
        </ModalOverlay>
      )}
    </>
  );
};

export default ProductDetail;
