import React from "react";

// style
import {
  ItemsUl,
  Amount,
  Container,
  CsText,
  Image,
  ItemInfo,
  ItemInfoText,
  ItemInfoTextInner,
  ItemLi,
  LiTitle,
  LiTitleBox,
  Name,
  PaymentInfo,
  Price,
  ReceiptBox,
  ReceiptInfo,
} from "../../styles/userProfileStyle/userBuyingStyle";

// icon
import { XIconCloseBtn } from "../button/XIconCloseBtn";

const items = [
  {
    image: "",
    name: "1",
    price: 10000,
    amount: 1,
  },
  {
    image: "",
    name: "2",
    price: 10000,
    amount: 1,
  },
  {
    image: "",
    name: "3",
    price: 10000,
    amount: 1,
  },
  {
    image: "",
    name: "4",
    price: 10000,
    amount: 1,
  },
  {
    image: "",
    name: "5",
    price: 10000,
    amount: 1,
  },
];

const PurchasedItems = () => {
  return (
    <Container>
      <ItemsUl>
        {items.map((item, idx) => (
          <ItemLi key={idx}>
            <LiTitleBox>
              <LiTitle></LiTitle>
              <XIconCloseBtn top="0" right="0" />
            </LiTitleBox>
            <ItemInfo>
              <Image>{item.image}</Image>
              <ItemInfoText>
                <ItemInfoTextInner>
                  <Name>{item.name}</Name>
                  <Amount>{item.amount} </Amount>
                </ItemInfoTextInner>
                <Price>{item.price.toLocaleString()}</Price>
                <CsText></CsText>
              </ItemInfoText>
            </ItemInfo>
            <ReceiptBox>
              <ReceiptInfo></ReceiptInfo>
              <PaymentInfo></PaymentInfo>
            </ReceiptBox>
          </ItemLi>
        ))}
      </ItemsUl>
    </Container>
  );
};

export default PurchasedItems;
