import React, { useEffect, useState } from "react";
import * as S from "../styles/SignupStyle";
import {
  isValidEmail,
  isValidPassword,
  isValidPhone,
  containSlang,
} from "../utils/validation";

import { useNavigate } from "react-router-dom";
import { checkEmail, formSubmit } from "../api/api";

const Signup = () => {
  const [email, setEmail] = useState("");
  const [emailMessage, setEmailMessage] = useState("");
  const [emailStatus, setEmailStatus] = useState(null);
  const [errors, setErrors] = useState({});
  const [isFormValid, setIsFormValid] = useState(false);
  const [formData, setFormData] = useState({
    email: "",
    password: "",
    phone_number: "",
    address: {
      city: "",
      zipcode: "",
    },
    gender: "",
    name: "",
    nickname: "",
  });

  const [terms, setTerms] = useState({
    allAgree: false,
    service: false,
    personalInfo: false,
    marketing: false,
  });

  const navigate = useNavigate();

  useEffect(() => {
    const formFieldsFilled =
      formData.email !== "" &&
      formData.password !== "" &&
      formData.confirmPassword !== "" &&
      formData.phone_number !== "" &&
      formData.address !== "" &&
      formData.gender !== "" &&
      formData.name !== "" &&
      formData.nickname !== "";

    const noErrors = Object.keys(errors).length === 0;

    const termsAccepted = terms.service && terms.personalInfo;

    setIsFormValid(formFieldsFilled && noErrors && termsAccepted);
  }, [formData, errors, terms]);

  const handleBlur = (e) => {
    const { name, value } = e.target;
    const newErrors = { ...errors };

    if (name === "email") {
      if (!isValidEmail(value)) {
        newErrors.email = "   .";
      } else {
        delete newErrors.email;
      }
    }

    if (name === "password") {
      if (!isValidPassword(value)) {
        newErrors.password =
          " 8 20  ,       .";
      } else {
        delete newErrors.password;
      }
    }

    if (name === "confirmPassword") {
      if (value !== formData.password) {
        newErrors.confirmPassword = "  .";
      } else {
        delete newErrors.confirmPassword;
      }
    }

    if (name === "phone_number") {
      if (!isValidPhone(value)) {
        newErrors.phone_number = "  000-0000-0000 .";
      } else {
        delete newErrors.phone_number;
      }
    }

    if (name === "nickname") {
      if (containSlang(value)) {
        newErrors.nickname = "    .";
      } else {
        delete newErrors.nickname;
      }
    }

    setErrors(newErrors); //   
  };

  //   
  const handleEmailCheck = async () => {
    try {
      const response = await checkEmail(email);

      if (response.status === 200) {
        setEmailMessage("  .");
        setEmailStatus(true);
      }
    } catch (error) {
      if (error.response) {
        const { status, data } = error.response;

        //   409 ( )
        if (status === 409) {
          setEmailMessage(data.message);
          setEmailStatus(false);
        }

        //   400 (  )
        if (status === 400 && data.field_errors) {
          const emailError = data.field_errors.find(
            (error) => error.field === "email"
          );

          if (emailError) {
            setEmailMessage(emailError.message);
          }
          setEmailStatus(false); //     
        }
      } else {
        console.error("       :", error);
        setEmailMessage("    ."); //   
        setEmailStatus(false); //      
      }
    }
  };

  //    
  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((prevData) => ({
      ...prevData,
      [name]: value,
    }));
  };

  //   
  const handleAddressSearch = () => {
    const script = document.createElement("script");
    script.src =
      "https://t1.daumcdn.net/mapjsapi/bundle/postcode/prod/postcode.v2.js";
    script.async = true;
    script.onload = () => {
      new window.daum.Postcode({
        oncomplete: (data) => {
          const { zonecode, address } = data;
          setFormData((prevData) => ({
            ...prevData,
            address: {
              ...prevData.address,
              zipcode: zonecode,
              city: address,
            },
          }));
        },
      }).open();
    };
    document.body.appendChild(script);
  };

  useEffect(() => {
    const formFieldsFilled =
      formData.email &&
      formData.password &&
      formData.confirmPassword &&
      formData.phone_number &&
      formData.address &&
      formData.gender &&
      formData.name &&
      formData.nickname;

    const noErrors = Object.keys(errors).length === 0;

    setIsFormValid(formFieldsFilled && noErrors);
  }, [formData, errors]);

  const handleGenderChange = (gender) => {
    setFormData((prevData) => ({
      ...prevData,
      gender: prevData.gender === gender ? "" : gender,
    }));
  };

  const handleTermsChange = (e) => {
    const { name, checked } = e.target;

    setTerms((prevTerms) => {
      const updatedTerms = {
        ...prevTerms,
        [name]: checked,
      };

      if (
        name !== "allAgree" &&
        (!updatedTerms.service ||
          !updatedTerms.personalInfo ||
          !updatedTerms.marketing)
      ) {
        updatedTerms.allAgree = false;
      }

      if (name === "allAgree") {
        updatedTerms.service = checked;
        updatedTerms.personalInfo = checked;
        updatedTerms.marketing = checked;
      }

      if (
        updatedTerms.service &&
        updatedTerms.personalInfo &&
        updatedTerms.marketing
      ) {
        updatedTerms.allAgree = true;
      }

      return updatedTerms;
    });
  };

  //   
  const handleSubmit = async (e) => {
    e.preventDefault();
    const { confirmPassword, ...dataToSend } = formData;
    try {
      const response = await formSubmit(dataToSend);
      if (response.message === "success signup") {
        navigate("/users/login");
        alert(" ");
      }
    } catch (error) {
      console.error("   :", error);
    }
  };

  return (
    <S.Container>
      <S.FormContainer>
        <S.Header></S.Header>
        <form onSubmit={handleSubmit}>
          <S.InputContainer>
            <S.SignupInput
              type="text"
              name="name"
              placeholder=" "
              value={formData.name}
              onChange={handleChange}
            />
          </S.InputContainer>
          <S.SignupInput
            type="text"
            name="nickname"
            placeholder=" "
            value={formData.nickname}
            onChange={handleChange}
            onBlur={handleBlur}
          />
          <S.InputContainer>
            <S.InputWrapper>
              <S.SignupInput
                type="email"
                name="email"
                placeholder=" "
                style={{ marginRight: "15px" }}
                value={formData.email}
                onChange={(e) => {
                  handleChange(e);
                  setEmail(e.target.value); //   
                }}
                onBlur={handleBlur}
              />
              <S.SmallButton type="button" onClick={handleEmailCheck}>
                 
              </S.SmallButton>
            </S.InputWrapper>
          </S.InputContainer>
          {errors.email && <S.ErrorMsg>{errors.email}</S.ErrorMsg>}
          {emailMessage && (
            <S.EmailStatusMessage success={emailStatus}>
              {emailMessage}
            </S.EmailStatusMessage>
          )}

          <S.SignupInput
            type="password"
            name="password"
            placeholder=" "
            value={formData.password}
            onChange={handleChange}
            onBlur={handleBlur}
          />
          {errors.password && <S.ErrorMsg>{errors.password}</S.ErrorMsg>}

          <S.SignupInput
            type="password"
            name="confirmPassword"
            placeholder=" "
            onChange={handleChange}
            onBlur={handleBlur}
          />
          {errors.confirmPassword && (
            <S.ErrorMsg>{errors.confirmPassword}</S.ErrorMsg>
          )}
          <S.SignupInput
            type="text"
            name="phone_number"
            placeholder=" "
            value={formData.phone_number}
            onChange={handleChange}
            onBlur={handleBlur}
          />
          {errors.phone_number && (
            <S.ErrorMsg>{errors.phone_number}</S.ErrorMsg>
          )}

          <S.InputWrapper>
            <S.SignupInput
              type="text"
              name="zipcode"
              placeholder=""
              style={{ marginRight: "15px" }}
              value={formData.address.zipcode}
              readOnly
            />
            <S.SmallButton type="button" onClick={handleAddressSearch}>
              
            </S.SmallButton>
          </S.InputWrapper>
          <S.SignupInput
            type="text"
            name="address"
            placeholder=" "
            value={formData.address.city}
            readOnly
          />
          <S.GenderWrapper>
            <S.GenderLabel>
              <S.Checkbox
                type="checkbox"
                name="male"
                checked={formData.gender === "MALE"}
                onChange={() => handleGenderChange("MALE")}
              />
              
            </S.GenderLabel>
            <S.GenderLabel>
              <S.Checkbox
                type="checkbox"
                name="female"
                checked={formData.gender === "FEMALE"}
                onChange={() => handleGenderChange("FEMALE")}
              />
              
            </S.GenderLabel>
          </S.GenderWrapper>
          <hr />
          <S.TermsContainer>
            <S.TermsHeader>
                     ,  14
                     .
            </S.TermsHeader>
            <S.TermsWrapper>
              <S.Checkbox
                type="checkbox"
                name="allAgree"
                checked={terms.allAgree}
                onChange={handleTermsChange}
              />
                .
            </S.TermsWrapper>
            <S.TermsWrapper>
              <S.Checkbox
                type="checkbox"
                name="service"
                checked={terms.service}
                onChange={handleTermsChange}
              />
              [ ] ()
            </S.TermsWrapper>
            <S.TermsWrapper>
              <S.Checkbox
                type="checkbox"
                name="personalInfo"
                checked={terms.personalInfo}
                onChange={handleTermsChange}
              />
              [    ] ()
            </S.TermsWrapper>
            <S.TermsWrapper>
              <S.Checkbox
                type="checkbox"
                name="marketing"
                checked={terms.marketing}
                onChange={handleTermsChange}
              />
                 ()
            </S.TermsWrapper>
          </S.TermsContainer>
          <S.SubmitButtonWrapper>
            <S.SubmitButton type="submit" disabled={!isFormValid}>
              
            </S.SubmitButton>
          </S.SubmitButtonWrapper>
        </form>
      </S.FormContainer>
    </S.Container>
  );
};

export default Signup;
