import React, { useState, useContext } from "react";
import { useNavigate } from "react-router-dom";
import { login } from "../../api/api";
import { isValidEmail, isValidPassword } from "../../utils/validation.js";
import * as L from "../../styles/LoginStyle";
import LoginButton from "./LoginButton.jsx";
import { UserContext } from "../../hook/context/UserContext.js";

const LoginForm = () => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [emailError, setEmailError] = useState(false);
  const [passwordError, setPasswordError] = useState("");
  const [error, setError] = useState("");
  const [isLoading, setIsLoading] = useState(false);
  const navigate = useNavigate();
  const { setUser } = useContext(UserContext);

  //   
  const handleEmailChange = (event) => {
    const value = event.target.value;
    setEmail(value);
    setEmailError(!value || !isValidEmail(value));
  };

  //   
  const handleEmailBlur = () => {
    if (!isValidEmail(email)) {
      setEmailError(true);
    }
  };

  //   
  const handlePasswordChange = (event) => {
    const value = event.target.value;
    setPassword(value);
    setPasswordError(
      !isValidPassword(value)
        ? "    8  20   ."
        : ""
    );
  };

  const handlePasswordBlur = () => {
    if (!isValidPassword(password)) {
      setPasswordError(
        "    8  20   ."
      );
    } else {
      setPasswordError("");
    }
  };

  //   
  const handleLogin = async (event) => {
    event.preventDefault();
    setIsLoading(true);
    setError("");

    if (emailError || passwordError) {
      setError("   .");
      setIsLoading(false);
      return;
    }

    try {
      const response = await login(email, password, setUser);
      console.log(" :", response);

      //       
      const accessToken = response.headers["authorization"].split(" ")[1];
      localStorage.setItem("accessToken", accessToken);

      //  storage   MainHeader  
      window.dispatchEvent(new Event("storage"));

      //   
      navigate("/");
      console.log("   .");
    } catch (error) {
      console.error("  :", error);
      setError(error.message || "    .");
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <form onSubmit={handleLogin}>
      <L.InputWrapper>
        <L.Input
          type="email"
          value={email}
          onChange={handleEmailChange}
          onBlur={handleEmailBlur}
          placeholder=" "
          error={emailError ? "true" : undefined}
        />
        {emailError && (
          <L.ErrorMessage>    .</L.ErrorMessage>
        )}
      </L.InputWrapper>
      <L.InputWrapper>
        <L.Input
          type="password"
          value={password}
          onChange={handlePasswordChange}
          onBlur={handlePasswordBlur}
          placeholder="  ."
          error={passwordError ? "true" : undefined}
        />
        {passwordError && <L.ErrorMessage>{passwordError}</L.ErrorMessage>}
      </L.InputWrapper>
      {error && <L.ErrorMessage>{error}</L.ErrorMessage>}
      <LoginButton isLoading={isLoading} />
    </form>
  );
};

export default LoginForm;
