import React, { useState } from "react";

import {
  Nav,
  Ul,
  Li,
  StyledNavLink,
  SellerLi,
  SellerBtnBox,
} from "../../styles/userProfileStyle/userNavStyle";
import { UniBtn } from "../button/UniBtn";

const UserNav = ({ isLogin, isSeller, handleSwitchUser }) => {
  return (
    <Nav>
      <Ul>
        <Li>
          <StyledNavLink to="/user" end>
            
          </StyledNavLink>
        </Li>
        <SellerLi isActive={isSeller}>
          <StyledNavLink to="/user/register">  </StyledNavLink>
        </SellerLi>
        <SellerLi isActive={isSeller}>
          <StyledNavLink to="/user/selling"> </StyledNavLink>
        </SellerLi>
        {/* <Li>
          <StyledNavLink to="/user/buying"> </StyledNavLink>
        </Li> */}
      </Ul>
      <SellerBtnBox>
        {isLogin && (
          <UniBtn onClick={handleSwitchUser}>
            {isSeller ? " " : " "}
          </UniBtn>
        )}
      </SellerBtnBox>
    </Nav>
  );
};

export default UserNav;
