import React, { useEffect, useState, useContext } from "react";
import { Link, useNavigate } from "react-router-dom";
import { logout } from "../../api/api";
import { Header, Logo, Img, IconBox, Icon } from "../../styles/MainHeaderStyle";
import { UserContext } from "../../hook/context/UserContext";
import LogoIcon from "../../icons/Ulogo.svg";
import UserIcon from "../../icons/user.svg";
import LoginIcon from "../../icons/login.svg";
import LogoutIcon from "../../icons/logout.svg";
import { useQuery, QueryClient } from "@tanstack/react-query";
import { getCategories } from "../../api/api";
import * as S from "../../styles/NavBarStyle";

const categoryMapping = {
  MALE: 1,
  FEMALE: 2,
  UNISEX: 3,
  CHILDREN: 4,
};

const MainHeader = () => {
  const { setUser } = useContext(UserContext);
  const [categories, setCategories] = useState([]);
  const [isAuthenticated, setIsAuthenticated] = useState(false); //    
  const navigate = useNavigate();
  const queryClient = new QueryClient();
  // localStorage    

  //        
  useEffect(() => {
    const token = localStorage.getItem("accessToken"); //    
    if (token) {
      setIsAuthenticated(true); //     
    }
    console.log("   :", isAuthenticated); //   
  }, []); //      

  // `localStorage`    `isAuthenticated`  
  useEffect(() => {
    const fetchCategories = async () => {
      try {
        const data = await getCategories();
        console.log(data);
        setCategories(data.categoryList);
      } catch (error) {
        console.error("Error fetching categories:", error);
      }
    };

    fetchCategories();
  }, []);
  //        
  useEffect(() => {
    const token = localStorage.getItem("accessToken"); //    
    if (token) {
      setIsAuthenticated(true); //     
    }
    console.log("   :", isAuthenticated); //   
  }, []); //      

  // `localStorage`    `isAuthenticated`  
  useEffect(() => {
    const handleStorageChange = () => {
      const token = localStorage.getItem("accessToken"); //     
      setIsAuthenticated(!!token); //   true,  false 
    };

    // `storage`   
    window.addEventListener("storage", handleStorageChange);

    //      
    return () => {
      window.removeEventListener("storage", handleStorageChange);
    };
  }, []);

  const handleCategoryClick = (categoryName) => {
    const categoryId = categoryMapping[categoryName];
    if (categoryId) {
      navigate(`/?category_id=${categoryId}`);
    }
  };
  // isAuthenticated        
  useEffect(() => {
    console.log("  : ", isAuthenticated); //    
  }, [isAuthenticated]); //    

  return (
    <Header>
      <S.StNavbarContainer>
        <Logo>
          <Link to="/">
            <Img src={LogoIcon} alt="logo" />
          </Link>
        </Logo>
        <S.StCategoriesContainer>
          <S.StCategoriesUl>
            {categories.map((category) => (
              <S.StCategoriesLi key={category.categoryName} onClick={() => handleCategoryClick(category.categoryName)}>
                {category.categoryName}
              </S.StCategoriesLi>
            ))}
          </S.StCategoriesUl>
        </S.StCategoriesContainer>
        <IconBox>
          <Link to="/Basket"></Link>
          <Link to="/signup"></Link>
          {isAuthenticated ? (
            //   (   )
            <Icon
              src={LogoutIcon}
              alt="logout-icon"
              onClick={() => {
                logout(navigate, setUser); //   
                setIsAuthenticated(false); //     false 
              }}
            />
          ) : (
            //   (    )
            <Link to="/users/login">
              <Icon src={LoginIcon} alt="login-icon" />
            </Link>
          )}
          <Link to="/user">
            <Icon src={UserIcon} alt="user-icon" />
          </Link>
        </IconBox>
      </S.StNavbarContainer>
    </Header>
  );
};

export default MainHeader;
